* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}


.App {
    text-align: center;
}

.App-logo {
    margin-top: 120px;
    height: 20vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    /*background-color: #464f60;*/
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
}

.App-header p {
    font-size: 2vh;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.MainHeader_header__3jG-X {
    width: 100%;
    height: 5rem;
    background-color: #044599;
    padding: 0 10%;
}

.MainHeader_header__3jG-X nav {
    height: 100%;
}

.MainHeader_header__3jG-X ul {
    height: 100%;
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: center;
}

.MainHeader_header__3jG-X li {
    margin: 0 1rem;
    width: 5rem;
}

.MainHeader_header__3jG-X a {
    color: white;
    text-decoration: none;
}

.MainHeader_header__3jG-X a:active,
.MainHeader_header__3jG-X a.MainHeader_active__11wpQ {
    color: #95bcf0;
    padding-bottom: 0.25rem;
    border-bottom: 4px solid #95bcf0;
}

.MainHeader_header__3jG-X a:hover {
    color: #95bcf0;
}


.container {
    justify-content: center;
    background: linear-gradient(45deg, rgba(145, 152, 229, 0.15), rgba(230, 100, 101, 0.2));
    max-width: 700px;
    margin: auto;
    border-radius: 20px;
    padding: 30px 10px 50px 10px;

}

.container h1 {
    font-size: 6vh;
    margin-bottom: 10px;
}

.container p {
    max-width: 450px;
    margin: auto;
    text-align: justify;
}

